<template>
  <v-ons-page>
    <v-ons-toolbar>
      <div class="left">
        <v-ons-back-button :on-click="pop">Back</v-ons-back-button>
      </div>
      <div class="center"><slot>プロフィール設定</slot></div>
    </v-ons-toolbar>
    <div class="register">
      <p class="complete">登録完了！</p>
      <h4>Step1</h4>
      <p>
        大学・専攻・興味を登録してあなたに近い先輩を探そう！
      </p>
      <v-ons-list>
        <v-ons-list-header>
          大学名で検索
        </v-ons-list-header>
        <v-ons-list-item>
          <v-ons-input
            placeholder="例）とうきょうだいがく"
            v-ons-model='university'
          >
          </v-ons-input>
        </v-ons-list-item>
      </v-ons-list>
      <v-ons-list>
        <v-ons-list-header>検索結果</v-ons-list-header>
        <v-ons-list-item v-for="item in searchItems">{{item}}</v-ons-list-item>
      </v-ons-list>
    </div>
  </v-ons-page>
</template>

<script>
import { mapActions } from 'vuex'
import Step2 from './Step2'

export default {
  name: 'register',
  props: ['pageStack'],
  data: function () {
    return {
      university: '',
      searchItems: []
    }
  },
  methods: {
    pop: function () {
      this.pageStack.pop()
    },
    gotoStep2: function () {
      this.hideLoading
      this.pageStack.push(Step2)
      console.log('hogehoge')
    },
    ...mapActions([
      'showLoading',
      'hideLoading'
    ])
  },
  watch: {
    university: function (newVal) {
      if (newVal === 'とう') {
        this.searchItems = [
          '東京大学',
          '東京学芸大学',
          '東京理科大学',
          '東京国際大学',
          '東京基督教大学'
        ]
      } else {
        this.searchItems = []
      }
    }
  }
}
</script>

<style scoped>
p {
  padding-right: 10px;
  padding-left: 10px;
  font-size: 15px;
}
p.complete {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}
h4 {
  padding-right: 10px;
  padding-left: 10px;
}
</style>



